import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  mode:'history',
  routes: [
    {
      path:'/',
      redirect:'/pages/home'
    },
    {
      path: '/pages/home',
      name: 'home',
      component: () => import('@/pages/home')
    }
  ]
})



